body {
  margin: 0;
  padding: 0;
  font-family: starjedi;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/groodymoloko/spaceballs.git/static/media/background.65cf4d05.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: black;
}

@font-face {
  font-family: "starjedi";
  src: url(/groodymoloko/spaceballs.git/static/media/starjedi-webfont.5461f41a.ttf);
}




.jumbot {
    background-image: url(/groodymoloko/spaceballs.git/static/media/rv.90c8b977.jpg);
    background-repeat: no-repeat;
    background-color: black;
    color: white;
    margin: 0px;
    height: 350px;
    text-align: center;
}

.contain {
    margin: 0px;
}

h1 {
    color: yellow;
    font-size: 72px;
}

.row {
    -webkit-justify-content: center;
            justify-content: center;
}

#message {
    color: yellow;
}
.card {
    width: 200px;
    height: 200px;
    margin: 10px;
    position: relative;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }

.img-container{
border-radius: 4px;
}

.card > .img-container {
height: 100%;
overflow: hidden;
}

.card > .img-container > img {
height: 100%;
}

.card:hover {
-webkit-transform: scale(1.03);
        transform: scale(1.03);
cursor: pointer;
}

.card:active {
-webkit-transform: translate(1px, 1px);
        transform: translate(1px, 1px);
}


